class SpeechRecognitionInterface {
    constructor(wordsList, onResultHandler) {
        this.handleResult = onResultHandler;
        this.grammar = `#JSGF V1.0; ${wordsList.join('\s|\s')} ;`;
        this.init();
    }

    check() {
        window.SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
        window.SpeechGrammarList = window.SpeechGrammarList || window.webkitSpeechGrammarList; 
        if (!('SpeechRecognition' in window)) {
            alert('Your browser doesn\'t support the API. Update it');

            return false;
         }

         return true;
    }

    init() {
        const isAvailable = this.check();

        if (!isAvailable) return;

        const SpeechRecognition = window.SpeechRecognition;
        const SpeechGrammarList = window.SpeechGrammarList;
        const recognition = new SpeechRecognition();
        const speechRecognitionList = new SpeechGrammarList();

        speechRecognitionList.addFromString(this.grammar, 1);
        recognition.grammars = speechRecognitionList;
        recognition.lang = 'en-US';
        recognition.interimResults = false;
        recognition.maxAlternatives = 1;
        recognition.onresult = this.handleResult;
        this.recognition = recognition;
    }
}

export default SpeechRecognitionInterface;