import { useState, useEffect } from 'react';
import { useLocation, useParams } from 'react-router-dom';
import { connect } from 'react-redux';

import * as actionCreators from '../../store/actions/index';
import { actions } from '../../store/reducers/spellWordReducer';
import Button from '../../components/UI/Button/Button';
import Spinner from '../../components/UI/Spinner/Spinner';
import SpeechRecognitionInterface from '../../../js_modules/interfaces/SpeechRecognitionInterface';
import ExplanationScreen from '../../components/ExplanationScreen/ExplanationScreen';
import {
	getSpreadSheetUrlWithRanges,
	getTaskPathByPageId,
	pathSheetIdPairs,
    ACTIVE_MODIFIER,
    CORRECT_MODIFIER,
    WRONG_MODIFIER,
    FIRST_CAPITAL_LETTER_CHARCODE,
} from '../../../constants';
import './SpellWord.scss';

const SpellWord = (props) => {
    const {
        fetchDataConnect,
        restoreInitialStateConnect,
        loading,
        data,
        error,
    } = props;
    const [permissionGranted, setPermissionGranted] = useState(false);
    const [currentIndex, setCurrentIndex] = useState(0);
    const [isSpeaking, setIsSpeaking] = useState(false);
    const [isCorrect, setIsCorrect] = useState(null);
    const [spelledText, setSpelledText] = useState('');
    const [isTranscriptionShown, setIsTranscriptionShown] = useState(false);
    const location = useLocation();
    const { token } = useParams();
    let recognition;

    useEffect(() => {
        return () => restoreInitialStateConnect();
    }, []);

    useEffect(() => {
        if (permissionGranted && !data) {
            const pageId = pathSheetIdPairs['/spell-word'];
            const requestUrl = getTaskPathByPageId(pageId);
            const {
                fetchStart: fetchStartHandler,
                fetchSuccess: fetchSuccessHandler,
                fetchFail: fetchFailHandler,
            } = actions;

            fetchDataConnect(
                getSpreadSheetUrlWithRanges(requestUrl, token),
                {
                    fetchStartHandler,
                    fetchSuccessHandler,
                    fetchFailHandler,
                },
            );
        }
    }, [permissionGranted]);

    useEffect(() => {
        if (currentIndex) {
            setIsCorrect(null);
            setSpelledText('');
            setIsTranscriptionShown(false);
        }
    }, [currentIndex]);

    const onResultHandler = (e) => {
        const word = e.results[0][0].transcript.toLowerCase();
        const confidencePercent = e.results[0][0].confidence;
        const currentWord = data[currentIndex].word;
        const correct = word === currentWord;
        if (correct) {
            // setCurrentIndex((prevState) => prevState += 1);
        } else {
            setSpelledText(word);
        }

        setIsCorrect(correct);
    };

    // recognition.onresult = onResultHandler;    
    const buttonsGroup = (
        <>
            <Button
                classNames="-secondary"
                onClickHandler={() => setPermissionGranted(true)}
            >
                Confirm
            </Button>
        </>
    );

    const explanationScreenProps = {
        header: 'Spell Word',
        subheader: 'Spell the given word correctly and score',
        rule: 'To continue with the task you gotta allow the app to use your mic',
        buttonsGroup,
    };

    const onSubmitHandler = (e) => {
        e.preventDefault();

        setIsSpeaking(true);
        recognition.start();
    };

    if (!permissionGranted) {
        return (
            <ExplanationScreen {...explanationScreenProps} />
        );
    }

    if (loading) {
		return  <Spinner />;
	}

    if (data && currentIndex < data.length - 1) {
        const {
            imageUrl,
            transcription,
            translation,
            word,
        } = data[currentIndex];
        const wordsList = data.map((el) => el.word);

        recognition = new SpeechRecognitionInterface(wordsList, onResultHandler).recognition;
        recognition.onaudioend = () => setIsSpeaking(false);

        return (
            <div className="SpellWord">
                <form onSubmit={onSubmitHandler}>
                    <figure className="word-block">
                        <div className="img-holder">
                            <img src={imageUrl} alt={word} />
                        </div>
                        <figcaption>{word}</figcaption>
                        <p className="translation">{translation}</p>
                        <p className="transcription" hidden={!isTranscriptionShown}>[{transcription}]</p>
                        <p
                            hidden={isCorrect === null}
                            className={`result mt-full ${isCorrect ? CORRECT_MODIFIER : WRONG_MODIFIER}`}
                        >
                            <b>
                                {isCorrect ? 'Correct' : `Wrong. You've spelt: ${spelledText}`}
                            </b>
                        </p>
                    </figure>
                    <div className="buttons-wrap">
                        <Button
                            type="submit"
                            classNames={`-pulse ${isSpeaking && ACTIVE_MODIFIER}`}
                        >
                            Spell
                        </Button>
                        <Button
                            classNames="-primary -wide"
                            onClickHandler={() => {
                                setCurrentIndex((prevState) => prevState += 1);
                            }}
                        >
                            Next â†’
                        </Button>
                        <Button
                            classNames="-secondary -wide"
                            onClickHandler={() => setIsTranscriptionShown(true)}
                        >
                            Show Transcription
                        </Button>
                    </div>
                </form>
            </div>
        );
    }
};

const mapStateToProps = (state) => ({
    data: state.spellWord?.fetchedData,
    loading: state.spellWord?.loading,
    error: state.spellWord?.error
});

const mapDispatchToProps = {
    fetchDataConnect: (url, options) => actionCreators.asyncFetchStart(url, options),
    restoreInitialStateConnect: actions.restoreInitialState,
};

export default connect(mapStateToProps, mapDispatchToProps)(SpellWord);