import PropTypes from 'prop-types';
import './ExplanationScreen.css';

/**
 * Renders entry point to tasks, explaining rules, grammar,
 * requesting user agreement to use browserAPI (e.g. SpeechSynthesis API).
 *
 * @param {object} props - Props.
 * @param {string} header - Header.
 * @param {string} [subheader] - Subheader.
 * @param {string|JSX.Element} rule - Rule description or/and markup.
 * @param {JSX.Element} buttonsGroup - Buttons with added handlers to them.
 * @returns {JSX.Element} Task initial screen.
 */
const ExplanationScreen = (props) => {
    const {
        header,
        subheader,
        rule,
        buttonsGroup,
    } = props;

    return (
        <div className='ExplanationScreen'>
            <h2>{header}</h2>
            {
                subheader
                ? <div className="subheader">{subheader}</div>
                : null
            }
            <div className="rule">{rule}</div>
            <div className="buttons-group">
                {buttonsGroup}
            </div>
        </div>
    );
};

ExplanationScreen.defaultProps = {
    subheader: '',
};

ExplanationScreen.propTypes = {
    header: PropTypes.string.isRequired,
    subheader: PropTypes.string,
    rule: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.element,
    ]).isRequired,
    buttonsGroup: PropTypes.element.isRequired,
};

export default ExplanationScreen;